/* Start header section */
.header {
	display: flex;
	background-color: var(--header-footer-color);
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 100px;
	padding: 1% var(--outside-padding);
}

.nav_list li {
	display: inline;
}

.nav_item {
	padding: 10px;
	font-size: x-large;
	text-decoration: none;
}

.nav_link:hover {
	font-weight: bolder;
	color: var(--second-background-color);
}
.active {
	font-weight: bolder;
	color: var(--second-background-color);
}

.logo_container {
	color: var(--contrast-color);
	text-decoration: none;
	font-size: xxx-large;
	font-weight: 900;
	transition: text-shadow 1s;
}

.logo_container:hover {
	text-shadow: 1px 1px 5px black, 0 0 0.1em white, 0 0 0.5em white;
	color: var(--main-background-color);
	color: black;
}
.hamburger {
	display: none;
	cursor: pointer;
}
.bar {
	display: block;
	width: 25px;
	height: 3px;
	margin: 5px auto;
	transition: all 0.3s ease-in-out;
	background-color: var(--contrast-color);
}

@media screen and (max-width: 825px) {
	.nav_item {
		font-size: 16px;
	} 
}

@media screen and (max-width: 686px) {
	.logo_container {
		font-size: x-large;
	}
    .hamburger {
		display: block;
		right: 5%;
	}

	.hamburger.active .bar:nth-child(2) {
		opacity: 0;
	}

	.hamburger.active .bar:nth-child(1) {
		-webkit-transform: translateY(8px) rotate(45deg);
		        transform: translateY(8px) rotate(45deg);
	}

	.hamburger.active .bar:nth-child(3) {
		-webkit-transform: translateY(-8px) rotate(-45deg);
		        transform: translateY(-8px) rotate(-45deg);
	}

	.nav_list {
		display: none;
	}

	.navbar_nav2 {
		position: fixed;
		z-index: 3;
		right: 5%;
		padding-top: 15px;
		flex-direction: column;
		text-align: center;
	}

	.nav_link {
		font-size: 16px;
		padding: 15px 20px;
	}
	.nav_item {
		margin: 0px 0px 5px 0px;	
		width: 130px;
		background-color: var(--header-footer-color);
		border: 1px solid black;
	}
}

@media screen and (max-width: 390px) {
	.nav_list li {
		display: flex;
	}
	.nav_item {
		margin-bottom: 3px;
		font-size: 16px;
	}
}

footer {
	background-color: var(--header-footer-color);
	width: 100%;
	height:60px;
}
footer ul {
	display: flex;
	background-color: var(--header-footer-color);
	justify-content: center;
	color: var(--contrast-color);
	font-size: x-large;
}

footer li {
	text-decoration: none;
	padding: 10px 20px;
	list-style-type: none;
}

.footer {
	text-decoration: none;
}
.footer:hover {
	font-weight: bolder;
	color: var(--second-background-color);
}

.svgGit {
	vertical-align: -0.125em;
	width: 1.5em;
	height: 1.5em;
}

.svgGit:hover {
	fill: var(--second-background-color);
}

.svgLinkedIn {
	vertical-align: -0.125em;
	width: 1.5em;
	height: 1.5em;
}

.svgLinkedIn:hover {
	fill: var(--second-background-color);
}
.medium {
	font-weight: bold;
	color: white;
	font-size: 40px;
}

@media screen and (max-width: 825px) {
	footer {
		font-size: large;
	}
}

.hero_section {
	display: flex;
	/* height: 90%; */
	margin-right: 20px;
	flex-direction: column;
	width: 100%;
	justify-content: center;
	text-align: right;
	overflow: none;
	min-height: calc(100vh - 160px);
}

.hero_img {
	position: relative;
	z-index: 1;
	max-width: 100%;
	height: calc(100vh - 160px);
}
 
.hero_content {
	position: absolute;
	display: flex;
	height: 90%;
	margin-right: 20px;
	flex-direction: column;
	width: 95%;
	justify-content: center;
	text-align: right;
	z-index: 2;
}

.hero_content h1 {
	color: var(--contrast-color);
	font-weight: bolder;
	font-size: 70px;
}
.hero_content h2 {
	color: var(--second-background-color);
	font-weight: bolder;
	align-self: right;
	font-size: 50px;
	padding-left: 60%;
	margin: 35px 0px;
}

.hero_content button {
	align-self: right;
	padding: 10px;
	font-size: 30px;
	border-radius: 0.5em;
	background-color: var(--highlight-color);
	color: var(--header-footer-color);
	font-weight: bolder;
}

.hero_content button:hover,
.hero_content button:focus {
	box-shadow: 2px 2px 2px 2px var(--main-background-color);
	-webkit-transform: translateY(-0.25em);
	        transform: translateY(-0.25em);
}

@media screen and (max-width: 1234px) {
	.hero_content {
		height: 70%;
	}
	.hero_content h1 {
		font-size: 45px;
	}
	.hero_content h2 {
		font-size: 35px;
	}
}

@media screen and (max-width: 968px) {
	.hero_content {
		height: 50%;
	}
	.hero_content h1 {
		font-size: 30px;
	}
	.hero_content h2 {
		font-size: 20px;
	}
}

@media screen and (max-width: 744px) {
	.hero_content {
		height: 30%;
	}

	.hero_content h1 {
		font-size: 30px;
	}
	.hero_content h2 {
		font-size: 20px;
	}
	.hero_content button {
		align-self: right;
		padding: 10px;
		font-size: 20px;
		border-radius: 0.5em;
		background-color: var(--highlight-color);
		color: var(--header-footer-color);
		font-weight: bolder;
	}
}

@media screen and (max-width: 504px) {
	.hero_content h1 {
		font-size: 25px;
	}
	.hero_content h2 {
		font-size: 16px;
	}
	.hero_content button {
		padding: 8px;
		font-size: 12px;
	}
}

@media screen and (max-width: 390px) {
	.hero_content h2 {
		margin: 10px 0px;
	}
}

/* Start About Me Section */
.aboutMe {
	background-color: var(--second-background-color);
	display: flex;
	min-height: Calc(100vh - 160px);
}

.aboutMe img {
	margin: var(--outside-padding) ;
	max-width: 380px;
	max-height: 560px;
	border-radius: 0.5rem;
}

.about_me_content {
	display: inline-block;
	margin: var(--outside-padding);
	color: var(--header-footer-color);
	font-size: large;
}

.aboutMe h4 {
	margin: 10px 0px;
	font-size: 25px;
	font-weight: bolder;
}

.about_me_content h2 {
	font-size: xxx-large;
	font-weight: bold;
	margin-bottom: 3%;
}
.aboutMe ul{
	margin-bottom: 10px;
}

@media screen and (max-width: 968px) {
	.aboutMe {
		flex-wrap: wrap;
		justify-content: center;
	}
	.aboutMe img {
		max-width: 50%;
	}
}
.resume_container {
	background-color: var(--second-background-color);
	min-height: Calc(100vh - 160px);
	padding: 30px 0px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

.skill_set {
	background-color: var(--highlight-color);
	margin: 0px 5% 30px;
	border-radius: 1rem;
}

.skill_set h3 {
	font-size: 30px;
	padding: 20px 20px 0 20px;
}

.skill_list {
	display: flex;
	justify-content: space-between;
	padding: 20px;
}
.skill_li {
	margin-bottom: 20px;
	font-size: 25px;
}

.skill_list ul:last-child{
	margin-bottom: 0px;
}

.technologies_container {
	background-color: var(--highlight-color);
	margin: 0px 5%;
	border-radius: 1rem;
}
.technologies_container h3 {
	font-size: 30px;
	padding: 20px 20px 0 20px;
	margin-bottom: 20px;
}

.technologies {
	font-size: xx-large;
	font-weight: 500;
}
.technologies_li {
	margin-left: 20px;
    padding-bottom: 20px;
    font-size: 25px;
}

.download_Link {
	background-color: var(--highlight-color);
	margin: 30px 5% 0px;
	border-radius: 1rem;
	display: flex;
	justify-content: center;
}

.download_aTag {
	color: black;
	font-size: 40px;
	font-weight: bold;
	margin: 20px;
	transition: text-shadow 1s;
}

.download_aTag:hover {
	text-shadow: 1px 1px 5px black, 0 0 0.1em rgb(5, 5, 5), 0 0 0.5em black;
	color: var(--main-background-color);
	color: white;
}

@media screen and (max-width: 900px) {
	.skill_list {
		flex-wrap: wrap;
		justify-content: left;
    }
}

/* Start Contact Information */
.contactMe {
	background-color: var(--second-background-color);
	padding: 30px;
	display: flex;
	min-height: Calc(100vh - 160px);
}
.contactMe h2 {
	color: var(--header-footer-color);
	font-weight: bolder;
	font-size: 50px;
}
.contactMe form {
	background-color: var(--highlight-color);
	flex-direction: column;
	width: 100%;
	margin-right: 30px;
	padding: 1% 2% 2% 2%;
	justify-content: space-around;
	display: flex;
	border-radius: 1rem;
}

.input_group {
	display: flex;
	width: 100%;
	justify-content: space-between;
}
.label {
	display: flex;
	width: 100%;
	justify-content: space-between;
}

.input {
	background: var(--main-background-color);
	border: none;
	color: aliceblue;
	padding: 10px 10px 5px 5px;
	font-size: 30px;
	border-radius: 0.5rem;
	margin:3px;
}
.input::-webkit-input-placeholder {
	color: aliceblue;
	font-size: 30px;
}
.input::placeholder {
	color: aliceblue;
	font-size: 30px;
}

textarea {
	background: var(--main-background-color);
	border: none;
	color: aliceblue;
	padding: 16px 178px 100px 5px;
	font-size: 30px;
	border-radius: 0.5rem;
}

textarea::-webkit-input-placeholder {
	color: aliceblue;
}

textarea::placeholder {
	color: aliceblue;
}

.input_group label {
	font-weight: bold;
	font-size: 30px;
}

.contactMe img {
	max-width: 40%;
	min-width: 559px;
	min-height: 664;
	border-radius: 0.5rem;
}

.error-text{
	margin-top: 20px;
}

.contactMe button {
	margin-top: 20px;
	background: var(--contrast-color);
	color: var(--header-footer-color);
	border-radius: 0.5rem;
	font-size: 30px;
	width: 30%;
}

.contactMe button:hover,
.contactMe button:focus {
	background-color: var(--header-footer-color);
	color: var(--contrast-color);
}

.aTag {
	padding-top:20px;
	color: black;
	font-size: 25px;
}

.aTag a:hover{
	color:blue;
}

