	}
	.projects p {
		margin-top: 6px;
		font-size: 10px;
	}
	.projects {
		min-width: 328px;
		min-height: 265px;
	}
}

:root {
	--main-background-color: #808080;
	--header-footer-color: #542344;
	--contrast-color: #d9d9d9;
	--highlight-color: #bfd1e5;
	--second-background-color: #d8bfaa;
	--outside-padding: 5%;
}

* {
	font-family: 'Inter', sans-serif;
	box-sizing: border-box;
}



